<template>
  <div class="mt-5 row justify-content-center">
    <form class="col-md-6" @submit.prevent="payOrder">
      <p class="h6">訂單明細</p>
      <table class="table">
        <thead>
          <th>品名</th>
          <th>數量</th>
          <th>單價</th>
        </thead>
        <tbody>
          <tr v-for="item in order.products" :key="item.id">
            <td class="align-middle">{{ item.product.title }}</td>
            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
            <td class="align-middle">{{ Math.floor(item.final_total) }}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="2" class="text-left">總計</td>
            <td>{{ Math.floor(order.total) }}</td>
          </tr>
        </tfoot>
      </table>

      <p class="h6">連絡人資料</p>
      <table class="table">
        <tbody>
          <tr>
            <th width="100">Email</th>
            <td class="text-center">{{ order.user.email }}</td>
          </tr>
          <tr>
            <th>姓名</th>
            <td class="text-center">{{ order.user.name }}</td>
          </tr>
          <tr>
            <th>收件人電話</th>
            <td class="text-center">{{ order.user.tel }}</td>
          </tr>
          <tr>
            <th>收件人地址</th>
            <td class="text-center">{{ order.user.address }}</td>
          </tr>
          <tr>
            <th>付款狀態</th>
            <td class="text-center">
              <span v-if="!order.is_paid">尚未付款</span>
              <span v-else class="text-success">付款完成</span>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-right" v-if="order.is_paid === false">
        <button class="btn btn-danger">確認付款去</button>
      </div>
      <div class="text-right" v-if="order.is_paid">
        <router-link class="btn btn-success" to="/frontProducts">繼續購物去</router-link>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      orderId: '',
      order: {
        user: {},
      },
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        console.log('getOrder()', response);
        vm.order = response.data.order;
        vm.isLoading = false;
      });
    },
    payOrder() {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.post(url).then((response) => {
        console.log('payOrder():', response);
        if (response.data.success) {
          vm.getOrder();
        }
        vm.isLoading = false;
      });
    },
    updateActiveOfCheckout(item) {
      this.$store.dispatch('updateCheckoutActive', item);
    },
  },
  created() {
    this.orderId = this.$route.params.orderId;
    this.getOrder();
    console.log('this.orderId:', this.orderId);
    this.updateActiveOfCheckout('checkout');
  },
};
</script>
